/**
 * Importaes internas do React necessrias para o funcionamento deste componente.
 * - React: permite a criao e manipulao de componentes React.
 * - useState: hook que possibilita a adio de estado a componentes funcionais.
 * - useEffect: hook utilizado para executar efeitos colaterais em componentes funcionais, como aes aps renderizaes.
 * - useRef: hook que fornece uma maneira de armazenar referncias a elementos DOM ou valores mutveis que persistem entre as renderizaes.
 * Importao da classe Chart do mdulo "chart.js/auto" para a renderizao de grficos.
 */
import React, { useState, useEffect, useRef } from "react";
import { Chart } from "chart.js/auto";

/**
 * Importaes de componentes e estilos j criados neste projeto.
 * - Loading: componente responsvel pela exibio de um indicador de carregamento.
 * - Message: componente utilizado para exibir mensagens ao usurio.
 * - Div1 e Div2: estilos importados do mdulo "../styles/dashboardBar", utilizados para estilizar elementos do componente DashboardBar.
 */
import Loading from "./loading";
import Message from "./message";
import { Div1, Div2 } from "../styles/dashboardBar";

export default function DashboardBar() {
  /**
   * Constantes de estado utilizadas neste componente para gerenciar diferentes aspectos da sua funcionalidade.
   * - loadingHistogram: estado que controla a exibio do indicador de carregamento para o histograma.
   * - histogramData: estado que armazena os dados do histograma.
   * - labeldash: estado utilizado para definir o rtulo do dashboard.
   * - myChart: estado que mantm a instncia do grfico criado com a biblioteca Chart.js.
   * - message: estado que controla a exibio de mensagens no componente.
   * - typeError: estado que define o tipo de erro ocorrido.
   * - messageError: estado que armazena a mensagem de erro a ser exibida.
   */
  const [histogramData, setHistogramData] = useState([]);
  const [labeldash, setLabelDash] = useState("");
  const [loadingHistogram, setLoadingHistogram] = useState(true);
  const [myChart, setMyChart] = useState(null);
  const [message, setMessage] = useState(false);
  const [typeError, setTypeError] = useState("");
  const [messageError, setMessageError] = useState("");

  /**
   * Constantes useRef utilizadas para referenciar elementos do DOM neste componente.
   * - selectPeriod: referncia ao elemento select utilizado para selecionar o perodo.
   * - dashboardBar: referncia ao elemento DOM que representa a barra do dashboard.
   * - timeoutBarUpdateRef: referncia ao elemento timeout para buscar informaes em determinado periodo.
   */
  const dashboardBar = useRef(null);
  const selectPeriod = useRef(null);
  const timeoutBarUpdateRef = useRef(null);

  /**
   * Varivel timeoutBarUpdate utilizada para armazenar o identificador do timeout responsvel pela atualizao contnua do dashboard.
   * barChartData: varivel que armazena os dados do dashboard em formato de string.
   */
  let barChartData = "";

  /**
   * Efeito colateral utilizado para buscar os dados da semana ao inicializar o componente.
   * - periodweek(): funo responsvel por buscar os dados da semana.
   * A dependncia vazia [] garante que o efeito ser executado apenas uma vez aps a montagem do componente.
   */
  useEffect(() => {
    periodweek();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  /**
   * Funo periodweek() utilizada para buscar os dados semanais para o dashboard.
   * - Reinicia o rtulo do dashboard e os dados do histograma.
   * - Realiza uma requisio GET para obter os dados da semana.
   * - Se o status da resposta for 210 (falta de dados), a funo tenta buscar os dados mensais e exibe uma mensagem informativa.
   * - Se a requisio for bem-sucedida, atualiza o rtulo do dashboard, chama a funo CallNewBar() para atualizar o grfico e define os dados do histograma.
   * - Em caso de erro, exibe uma mensagem de erro.
   */
  function periodweek() {
    setLabelDash("");
    setHistogramData([]);
    fetch("getDashBoardBar/week/", {
      method: "GET",
      headers: {
        Accept: "application/json",
        "Cache-Control": "no-cache",
      },
    })
      .then((response) => {
        if (response.status === 210) {
          try {
            selectPeriod.current.value = "2";
            setMessage(true);
            setTypeError("Falta de Dados");
            setMessageError("Buscando Chamados do Ms");
            barChartData = "";
            periodMonth();
          } catch (err) {
            console.log(err);
          }
        }
        return response.json();
      })
      .then((data) => {
        barChartData = "week";
        setLabelDash("Chamados da Semana");
        CallNewBar();
        setHistogramData(data);
      })
      .catch((err) => {
        setMessage(true);
        setTypeError("Fatal Error");
        setMessageError(err);
      });
  }

  /**
   * Funo periodMonth() utilizada para buscar os dados mensais para o dashboard.
   * - Reinicia o rtulo do dashboard e os dados do histograma.
   * - Realiza uma requisio GET para obter os dados do ms.
   * - Se o status da resposta for 210 (falta de dados), a funo tenta buscar os dados anuais e exibe uma mensagem informativa.
   * - Se a requisio for bem-sucedida, atualiza o rtulo do dashboard, define os dados do histograma e chama a funo CallNewBar() para atualizar o grfico.
   * - Em caso de erro, exibe uma mensagem de erro.
   */
  function periodMonth() {
    setLabelDash("");
    setHistogramData([]);
    fetch("getDashBoardBar/updateMonth/", {
      method: "GET",
      headers: {
        Accept: "application/json",
        "Cache-Control": "no-cache",
      },
    })
      .then((response) => {
        if (response.status === 210) {
          selectPeriod.current.value = "3";
          setMessage(true);
          setTypeError("Falta de Dados");
          setMessageError("Buscando Chamados do Ano");
          barChartData = "";
          periodYear();
        }
        if (response.status === 302) {
          return window.location.href("/login");
        }
        return response.json();
      })
      .then((data) => {
        barChartData = "month";
        setLabelDash("Chamados do Ms");
        setHistogramData(data);
        CallNewBar();
      })
      .catch((err) => {
        setMessage(true);
        setTypeError("Fatal Error");
        setMessageError(err);
      });
  }

  /**
   * Funo periodYear() utilizada para buscar os dados anuais para o dashboard.
   * - Reinicia o rtulo do dashboard e os dados do histograma.
   * - Realiza uma requisio GET para obter os dados do ms.
   * - Se o status da resposta for 210 (falta de dados), a funo tenta buscar os dados totais e exibe uma mensagem informativa.
   * - Se a requisio for bem-sucedida, atualiza o rtulo do dashboard, define os dados do histograma e chama a funo CallNewBar() para atualizar o grfico.
   * - Em caso de erro, exibe uma mensagem de erro.
   */
  function periodYear() {
    setLabelDash("");
    setHistogramData([]);
    fetch("getDashBoardBar/updateYear/", {
      method: "GET",
      headers: {
        Accept: "application/json",
        "Cache-Control": "no-cache",
      },
    })
      .then((response) => {
        if (response.status === 210) {
          selectPeriod.current.value = "4";
          setMessage(true);
          setTypeError("Falta de Dados");
          setMessageError("Buscando Todos os Chamados");
          barChartData = "";
          periodAll();
        }
        return response.json();
      })
      .then((data) => {
        barChartData = "year";
        setLabelDash("Chamados deste Ano");
        setHistogramData(data);
        CallNewBar();
      })
      .catch((err) => {
        setMessage(true);
        setTypeError("Fatal Error");
        setMessageError(err);
      });
  }

  /**
   * Funo periodYear() utilizada para buscar os dados totais para o dashboard.
   * - Reinicia o rtulo do dashboard e os dados do histograma.
   * - Realiza uma requisio GET para obter os dados do ms.
   * - Se o status da resposta for 210 (falta de dados), exibe uma mensagem informativa.
   * - Se a requisio for bem-sucedida, atualiza o rtulo do dashboard, define os dados do histograma e chama a funo CallNewBar() para atualizar o grfico.
   * - Em caso de erro, exibe uma mensagem de erro.
   */
  function periodAll() {
    fetch("getDashBoardBar/updateAll/", {
      method: "GET",
      headers: {
        Accept: "application/json",
        "Cache-Control": "no-cache",
      },
    })
      .then((response) => {
        if (response.status === 210) {
          selectPeriod.current.value = "4";
          setMessage(true);
          setTypeError("Falta de Dados");
          setMessageError("Nenhum Chamado Foi Criado");
          setLoadingHistogram(false);
          barChartData = "";
        }
        return response.json();
      })
      .then((data) => {
        barChartData = "all";
        setLabelDash("Todos os Chamados");
        setHistogramData(data);
        CallNewBar();
      })
      .catch((err) => {
        setMessage(true);
        setTypeError("Fatal Error");
        setMessageError(err);
      });
  }

  /**
   * Acionado sempre que os dados do dashboard (histogramData) so atualizados.
   * - A funo initChart()  chamada para inicializar o grfico com os novos dados.
   * - Verifica se os dados do histograma esto presentes e vlidos antes de criar o grfico.
   * - Se o grfico j existir, ele  destrudo para evitar duplicatas.
   * - Cria um novo grfico utilizando os dados atualizados.
   * - Define o novo grfico como estado.
   * - Exibe a barra do dashboard e desativa o indicador de carregamento.
   * - Em caso de erro, exibe uma mensagem de erro.
   * - O retorno da funo de limpeza  responsvel por destruir o grfico ao desmontar o componente.
   * -  executado apenas quando os dados do histograma so modificados.
   */
  useEffect(() => {
    const initChart = () => {
      try {
        if (histogramData && histogramData.days && histogramData.values) {
          if (myChart) {
            myChart.destroy();
          }
          const dashboard = document.getElementById("dashboard");
          const newChart = new Chart(dashboard, {
            type: "bar",
            data: {
              labels: histogramData.days,
              datasets: [
                {
                  label: [labeldash],
                  data: histogramData.values,
                },
              ],
            },
          });

          setMyChart(newChart);

          dashboardBar.current.style.display = "block";
          setLoadingHistogram(false);
        } else {
          dashboardBar.current.style.display = "block";
          setLoadingHistogram(false);
        }
      } catch (err) {
        setMessage(true);
        setTypeError("Fatal Error");
        setMessageError(err);
        console.log(err);
      }
    };

    initChart();

    return () => {
      if (myChart) {
        myChart.destroy();
      }
    };

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [histogramData]);

  /**
   * Funo CallNewBar() utilizada para reativar a funo de busca de dados do dashboard a cada 1 minuto, caso existam dados.
   * - Se houver um timeoutBarUpdate anteriormente definido, ele  limpo para evitar mltiplas execues.
   * - Determina a funo de atualizao com base no tipo de dados do dashboard selecionado.
   * - Define um novo timeoutBarUpdate para chamar a funo de atualizao a cada 1 minuto.
   */
  function CallNewBar() {
    if (timeoutBarUpdateRef.current) {
      clearTimeout(timeoutBarUpdateRef.current);
    }

    let updateFunction;
    const currentBarChartData = barChartData;

    switch (currentBarChartData) {
      case "week":
        updateFunction = periodweek;
        break;
      case "month":
        updateFunction = periodMonth;
        break;
      case "year":
        updateFunction = periodYear;
        break;
      case "all":
        updateFunction = periodAll;
        break;
      default:
        return;
    }

    timeoutBarUpdateRef.current = setTimeout(() => {
      updateFunction();
    }, 60000);
  }

  /**
   * Funo changePeriod() acionada pelo evento de seleo do perodo na interface do usurio.
   * - Obtm o valor selecionado no elemento select de perodo.
   * - Com base no valor selecionado, chama a funo correspondente para buscar os dados do dashboard.
   * - Em caso de valor invlido, exibe uma mensagem de erro.
   */
  function changePeriod() {
    const period = selectPeriod.current.options[selectPeriod.current.selectedIndex].value;

    switch (period) {
      case "1":
        periodweek();
        break;
      case "2":
        periodMonth();
        break;
      case "3":
        periodYear();
        break;
      case "4":
        periodAll();
        break;
      default:
        console.log("periodo invlido: ", period);
        setMessage(true);
        setTypeError("Fatal Error");
        setMessageError("Periodo invlido:", period);
        break;
    }
  }

  return (
    <Div1 className="mt-5 mb-5 position-relative">
      {message && (
        <div className="position-absolute top-50 start-50 translate-middle z-1">
          <Message
            TypeError={typeError}
            MessageError={messageError}
            CloseMessage={() => {
              setTypeError("");
              setMessageError("");
              setMessage(false);
              return;
            }}
          />
        </div>
      )}
      <div>
        <div className="h-100 w-100 d-flex justify-content-center">{loadingHistogram && <Loading />}</div>
        <Div2 className="d-flex flex-column">
          <select className="form-select" ref={selectPeriod} onChange={changePeriod} defaultValue="1">
            <option value="1" selected>
              Est Semana
            </option>
            <option value="2">Este Ms</option>
            <option value="3">Este Ano</option>
            <option value="4">Todo Perodo</option>
          </select>
          <canvas id="dashboard" ref={dashboardBar}></canvas>
        </Div2>
      </div>
    </Div1>
  );
}
